import axios from 'axios';
import { API_BASE_URL } from '../../Config/Api';
import { LOGIN_FAILURE, LOGIN_REQUEST, LOGIN_SUCCESS } from './auth.actionType';
import { REGISTER_REQUEST, REGISTER_SUCCESS, REGISTER_FAILURE } from "./auth.actionType";

export const loginUserAction=(loginData)=>async(dispatch)=>{
    dispatch({type:LOGIN_REQUEST})
    try{
        const { data } = await axios.post(`${API_BASE_URL}/api/public/login`, loginData,{
            headers: {
                "Content-Type": "application/json",
            },
        });
        if(data.jwt){
            localStorage.setItem("jwt",data.jwt)
        }
        console.log("login successfully",data)
        dispatch({type:LOGIN_SUCCESS,payload:data.jwt})
    }catch(error){
        console.log("------",error)
        dispatch({type:LOGIN_FAILURE,payload:error})
    }
    
}
export const registerUserAction = (registerData) => async (dispatch) => {
    dispatch({ type: REGISTER_REQUEST });
    try {
        console.log("Register Payload:", registerData);
      const { data } = await axios.post(`${API_BASE_URL}/api/users/register`, registerData.data);
      console.log("Registration successful", data);
      dispatch({ type: REGISTER_SUCCESS, payload: data });
    } catch (error) {
      console.error("Registration failed", error);
      dispatch({ type: REGISTER_FAILURE, payload: error });
    }
  };