import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import '../../CSS/Home/Navbar.css';
import menu from '../../Assets/SVG/menu.svg';
import closemenu from '../../Assets/SVG/close-menu.svg';
import { motion } from 'framer-motion';
import user from '../../Assets/SVG/sign-in-user.svg'
import { Link } from 'react-router-dom';

export default function Navbar() {
  const [isOpen, setIsOpen] = useState(false);

  const toggleMenu = () => {
    setIsOpen(!isOpen);
  };
  
const navigate = useNavigate();


  return (
    <>
      <div id="main">

        <nav>

          <div id="mob-nav">

            <div id='mobile-navbar'>

              <div className="menu-toggle" onClick={toggleMenu}>
                <img src={menu} alt="" />
              </div>
              <div id='mob-login'onClick={()=>navigate("/Login")}>
                <Link to="/Login">Sign in <img src={user} id='user' alt="" /></Link>
              </div>

            </div>


            {/* side menu start  */}

            <div className="menu-container">

              <motion.div className={`side-menu ${isOpen ? 'open' : 'closed'}`} initial={{ x: '-100%' }} animate={{ x: isOpen ? 0 : '-100%' }} transition={{ duration: 0.3 }}>
                <div className='menu-list'>

                  <div id='close-menu'>
                    <h3>Menu</h3>
                    <img onClick={toggleMenu} src={closemenu} alt="" />
                  </div>

                  {/* side menu list  */}
                  <ul onClick={()=>navigate("/HomeXpress.com")}>
                    <li><Link to="/HomeXpress.com"><i className="ri-home-5-fill menu-svg"></i> Home</Link></li>
                    {/* <li><a to="/"><i className="ri-home-smile-fill menu-svg"></i> </a></li> */}
                  </ul>
                  <ul>
                    <li><Link to="/Register"><i className="ri-user-fill menu-svg"></i> Seller Login</Link></li>
                    {/* <li><a to="/"><i className="ri-layout-grid-2-fill menu-svg"></i> All Categories</a></li> */}
                  </ul>

                  <ul>
                    <li><Link to="/MainServices"><i className="ri-discount-percent-fill menu-svg"></i> Offer Zone</Link></li>
                  </ul>

                  <ul>
                    <li><Link to="/"><i className="ri-account-circle-fill menu-svg"></i> My Account</Link></li>
                    <li><Link to="/"><i className="ri-takeaway-fill menu-svg"></i> My Orders </Link></li>
                  </ul>

                  <ul>
                    <li><Link to="/"><i className="ri-customer-service-2-fill menu-svg"></i> Help Center</Link></li>
                  </ul>

                </div>
              </motion.div>
            </div>
            {/* side menu end  */}

          </div>



          {/* Desktop Navbar  */}

          <div id="desktop-nav">

            <div id="nav-part1">
              <input type="text" placeholder="Search for Services :)" />
              <span><i class="ri-search-line"></i></span>
            </div>

            <div id="nav-part2">
              <Link to="/HomeXpress.com" className="a-menu"><i className="ri-home-heart-fill"></i> Home</Link>
              <Link to="/Login" className="a-menu"><i className="ri-login-box-line"></i> Login</Link>
              <Link to="/Login" className="a-menu"><i className="ri-shopping-cart-2-line"></i> Cart</Link>
            </div>

            <div id='user-profile'>
              <Link to="/Profile"><i className="ri-account-circle-line"></i></Link>
            </div>

            {/* Desktop Navbar  end  */}

          </div>

        </nav>
      </div>
    </>
  );
}
