import React from 'react';
import '../../CSS/Services/MianServices.css';
import { useNavigate } from 'react-router-dom';
// images import 
import repair from '../../Assets/Img/MainServicesImg/ApplianceRepair.png';
import Electrician from '../../Assets/Img/MainServicesImg/electrician.png';
import carpenter from '../../Assets/Img/MainServicesImg/carpenter.png';
import clean from '../../Assets/Img/MainServicesImg/cleaning.png';
import painting from '../../Assets/Img/MainServicesImg/painting.png';
import Ac from '../../Assets/Img/AcAdImg/7.png';

function MainServices() {
    const navigate = useNavigate();
    return (
        <>
            {/* service catagiories     */}

            <div className="service-div">

                <div className='sub-menu-top'>
                    <h3>What are you looking for...?</h3>
                    <p>
                        <i className="ri-arrow-right-line" id='sub-menu-right-arrow'></i>
                    </p>
                </div>


                <div className='main-ser-container'>
                    <div className="sub-menu">


                        <div className="box" onClick={() => navigate("/Appliance")}>
                            <div className="item">
                                <img src={repair} alt="repair-img" />
                            </div>

                            <div className='service-info'>
                                <p>Appliance Repair </p>
                                <p>Upto 35% off</p>
                            </div>
                            <div className='Main-Ratings'>
                                <span><i className="ri-star-s-fill"></i></span>
                                <span><i className="ri-star-s-fill"></i></span>
                                <span><i className="ri-star-s-fill"></i></span>
                                <span><i className="ri-star-s-fill"></i></span>
                                <span><i className="ri-star-s-fill"></i></span>
                            </div>

                        </div>

                        <div className="box" onClick={() => navigate("/Cleaning")}>
                            <div className="item">
                                <img src={clean} alt="Cleaning" />
                            </div>
                            <div className="service-info">
                                <p>Cleaning</p>
                                <p>Upto 60% off</p>
                            </div>
                            <div className='Main-Ratings'>
                                <span><i className="ri-star-s-fill"></i></span>
                                <span><i className="ri-star-s-fill"></i></span>
                                <span><i className="ri-star-s-fill"></i></span>
                                <span><i className="ri-star-s-fill"></i></span>
                                <span><i className="ri-star-s-fill"></i></span>
                            </div>
                        </div>

                        <div className="box" onClick={() => navigate("/Painting")}>
                            <div className="item">
                                <img src={painting} alt="Painting" />
                            </div>
                            <div className="service-info">
                                <p>Painting </p>
                                <p>Upto 85% off</p>
                            </div>
                            <div className='Main-Ratings'>
                                    <span><i className="ri-star-s-fill"></i></span>
                                    <span><i className="ri-star-s-fill"></i></span>
                                    <span><i className="ri-star-s-fill"></i></span>
                                    <span><i className="ri-star-s-fill"></i></span>
                                    <span><i className="ri-star-s-fill"></i></span>
                                </div>
                        </div>

                        <div className="box" onClick={() => navigate("/Electrician")}>
                            <div className="item">
                                <img src={Electrician} alt="Electrician" />
                            </div>
                            <div className="service-info">
                                <p>Electrician</p>
                                <p>Upto 75% off</p>
                            </div>
                            <div className='Main-Ratings'>
                                    <span><i className="ri-star-s-fill"></i></span>
                                    <span><i className="ri-star-s-fill"></i></span>
                                    <span><i className="ri-star-s-fill"></i></span>
                                    <span><i className="ri-star-s-fill"></i></span>
                                    <span><i className="ri-star-s-fill"></i></span>
                                </div>
                        </div>

                        <div className="box" onClick={() => navigate("/Ac")}>
                            <div className="item">
                                <img src={Ac} alt="Air Condition Services" />
                            </div>
                            <div className="service-info">
                                <p>Air Condition Services</p>
                                <p>Upto 25% off</p>
                            </div>
                            <div className='Main-Ratings'>
                                    <span><i className="ri-star-s-fill"></i></span>
                                    <span><i className="ri-star-s-fill"></i></span>
                                    <span><i className="ri-star-s-fill"></i></span>
                                    <span><i className="ri-star-s-fill"></i></span>
                                    <span><i className="ri-star-s-fill"></i></span>
                                </div>
                        </div>

                        <div className="box" onClick={() => navigate("/Carpenter")}>
                            <div className="item">
                                <img src={carpenter} alt="Carpenter" />
                            </div>
                            <div className="service-info">
                                <p>Carpenters</p>
                                <p>Upto 65% off</p>
                            </div>
                            <div className='Main-Ratings'>
                                    <span><i className="ri-star-s-fill"></i></span>
                                    <span><i className="ri-star-s-fill"></i></span>
                                    <span><i className="ri-star-s-fill"></i></span>
                                    <span><i className="ri-star-s-fill"></i></span>
                                    <span><i className="ri-star-s-fill"></i></span>
                                </div>
                        </div>

                    </div>
                </div>
            </div>

        </>
    )
}

export default MainServices
