import { LOGIN_FAILURE, LOGIN_REQUEST, LOGIN_SUCCESS } from "./auth.actionType"

const initialState={
    jwt:null,
    error:null,
    loading:false
}

export const authReducer=(state=initialState,action)=>{
    switch(action.type){
        case LOGIN_REQUEST:
            return{...state, loading:true,error:null}
        case LOGIN_SUCCESS:
            console.log("JWT received:", action.payload);
            return{...state, jwt:action.payload,loading:false,error:null}
            case LOGIN_FAILURE:
                console.log("Login error:", action.payload.response?.data || action.payload.message);
                return { ...state, loading: false, error: action.payload.response?.data || action.payload.message };
            
        default:
            return state;
            }
    }
