import React, { useState } from 'react';
import '../../CSS/Home/Login.css';
import { Link } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { registerUserAction } from '../../Redux/Auth/auth.action';
import { useNavigate } from 'react-router-dom';

export default function Register() {  // ✅ Correct, only one function
    const [formData, setFormData] = useState({
        username: "",
        email: "",
        phone: "",
        address: "",
        password: "",
    });
    const navigate = useNavigate();

    const dispatch = useDispatch();

    const handleChange = (e) => {
        setFormData({ ...formData, [e.target.name]: e.target.value });
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        dispatch(registerUserAction({ data: formData }));
    };

    return (
        <div>
            <div className="center">
                <h1>Sign up</h1>
                <form method="post" onSubmit={handleSubmit}>
                    <div className="txt_field">
                        <input type="text"
                            name="username"
                            value={formData.username}
                            onChange={handleChange}
                            required />
                        <span></span>
                        <label>Full Name</label>
                    </div>
                    <div className="txt_field">
                        <input type="email"
                            name="email"
                            value={formData.email}
                            onChange={handleChange}
                            required />
                        <span></span>
                        <label>Email</label>
                    </div>
                    <div className="txt_field">
                        <input type="password"
                            name="password"
                            value={formData.password}
                            onChange={handleChange}
                            required />
                        <span></span>
                        <label>Password</label>
                    </div>

                    <div className="txt_field">
                        <input type="text"
                            name="address"
                            value={formData.address}
                            onChange={handleChange}
                            required />
                        <span></span>
                        <label>Address</label>
                    </div>
                    <div className="txt_field">
                        <input type="number"
                            name="phone"
                            value={formData.phone}
                            onChange={handleChange}
                            required />
                        <span></span>
                        <label>Phone</label>
                    </div>

                    <input type="submit" value="Sign up" onClick={() => navigate("/login")} />
                    <div className="signup_link">Already have an account ? Log in here <Link to="/login">Login :)   </Link></div>
                </form>
            </div>
        </div>
    );
}
