import React from 'react'
import '../../CSS/Home/Profile.css'
export default function Profile() {
    return (
        <div>
            <div id='ProfieMainContainer'>



                <div className='UserProfile'>

                    <div>

                        <div className='SubProfile'>

                            <div className='UserImage'>
                                <img src="https://templates.joomla-monster.com/joomla30/jm-news-portal/components/com_djclassifieds/assets/images/default_profile.png" alt="" />

                            </div>

                            <div className='UserProfileEdite'>
                                <p><i class="ri-edit-2-fill"></i></p>
                            </div>

                            <div className='UserName'>
                                <h3>Suraj Pujari</h3>
                            </div>

                        </div>


                        <div className='UserProfileSection'>
                            <div className='PersonalInfo PersonalInformation '>
                                <span><i class="ri-user-fill"></i></span>
                                <span>Personal Info</span>
                            </div>

                            <div className='LogOut  PersonalInformation'>
                                <span><i class="ri-logout-box-r-line"></i></span>
                                <span>Log Out</span>

                            </div>
                        </div>
                    </div>

                </div>


                <div className='ProfileUserInfo'>
                    <h3>Personal Inforamation</h3>

                    <div>
                        <form action="">



                            <div className='Pesonal-Info'>

                                <div className='Fname'>
                                    <label for="fname">First Name</label><br />
                                    <input type="text" id="fname" name="fname" required />
                                </div>

                                <div className='Lname'>
                                    <label for="fname">Last Name</label> <br />
                                    <input type="text" id="fname" name="fname" required/>
                                </div>

                            </div>


                            <div className='UserEmail' >

                                <label for="email">Email</label><br/>
                                <input type="email" required/><br />

                                <label for="text">Address</label><br />
                                <input type="text" required />

                            </div>

                            <div className='GenderInput'>

                                <div className='UserNumber'>
                                    <label for="fname">Phone Number</label><br />
                                    <input type="number" id="fname" name="fname" required />
                                </div>

                                <div className='Gender'>
                                    <label for="male">Male</label>
                                    <input type="radio" id="male" name="gender" value="male"required />

                                    <label for="female">Female</label>
                                    <input type="radio" id="female" name="gender" value="female" required />

                                </div>

                            </div>

                            <div className='InfoSave'>
                                <button className='SaveChanges'>Save Changes</button>
                                <button className='DiscardChanges'>Discard Changes</button>
                            </div>

                        </form>
                    </div>


                </div>


            </div>

        </div>
    )
}
